<h3>Usage</h3>
<h4>component</h4>
<p>If you need to customize the contents of the Modal, you can achieve it</p>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"header"</span>&gt;</span>...<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"body"</span>&gt;</span>...<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span>&gt;</span>...<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<h4>Methods</h4>
<p>N3 provides a function-driven one-time modal, invoked by the instance attributes n3Modal.alert and n3Modal.confirm, which are dynamically bound to the DOM tree when used and automatically destroyed after shutdown.</p>
<p>In order to restore the native dialog operation experience, the function-driven Modal will not be closed by clicking on the background.</p>
<pre v-pre=""><code v-pre="" class="language-javascript"><span class="hljs-keyword">const</span> options = {...}

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    showAlert () {
      <span class="hljs-keyword">this</span>.n3Modal.alert(options)
    },
    showConfirm () {
      <span class="hljs-keyword">this</span>.n3Modal.confirm(options)
    }
  }
}

</code></pre>
