  methods: {
    showModal () {
      this.$refs.modal.open()
    },
    showBigModal () {
      this.$refs.bigModal.open()
    },
    showZoomModal () {
      this.$refs.zoomModal.open()
    },
    showCustomModal () {
      this.$refs.customModal.open()
    },
    show () {
      console.log('show')
    },
    showConfirm () {
      this.n3Modal.confirm({
        title: 'Title',
        message: 'This is a Modal',
        effect: 'zoom',
        type: 'info',
        onShow () {
          console.log('open')
        },
        onConfirm () {
          console.log('confirm')
        },
        onHide () {
          console.log('hide')
        }
      })
    },
    showAlert () {
      this.n3Modal.alert({
        title: 'title',
        message: 'This is a Modal',
        effect: 'fade',
        type: 'success',
        onShow () {
          console.log('open')
        },
        onHide () {
          console.log('hide')
        }
      })
    }
  }
}
</script>


