<template><section><template>
<h3>Typeahead</h3>
<div class="bs-docs-section">
<div class="bs-example">
<blockquote>
<p>Loale data</p>
</blockquote>
<p><n3-typeahead v-model="value" :data="USstate" placeholder="input">
</n3-typeahead></p>
<hr>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">n3-typeahead</span> 
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>
    <span class="hljs-attr">:data</span>=<span class="hljs-string">"USstate"</span> 
    <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"input"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">n3-typeahead</span>&gt;</span>

</code></pre>
<hr>
<blockquote>
<p>async</p>
</blockquote>
<p><n3-typeahead async placeholder="CCCAddress, async via maps.googleapis.com" @change="getResult" :item-render="itemRender" :items="items" :add-format="googleCallback">
</n3-typeahead></p>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">n3-typeahead</span> 
  <span class="hljs-attr">async</span>
  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"CCCAddress, async via maps.googleapis.com"</span>
  <span class="hljs-attr">@change</span>=<span class="hljs-string">"getResult"</span>
  <span class="hljs-attr">:item-render</span>=<span class="hljs-string">"itemRender"</span>
  <span class="hljs-attr">:items</span>=<span class="hljs-string">"items"</span>
  <span class="hljs-attr">:add-format</span>=<span class="hljs-string">"googleCallback"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">n3-typeahead</span>&gt;</span>
</code></pre>
<hr>
<blockquote>
<p>内容模版</p>
</blockquote>
<p><n3-typeahead async :context="_self" placeholder="Github users, async via api.github.com" @change="getGitresults" :items="gitItems" :item-render="itemRendergit" :add-format="githubCallback">
</n3-typeahead></p>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">n3-typeahead</span>
  <span class="hljs-attr">async</span> 
  <span class="hljs-attr">:context</span>=<span class="hljs-string">"_self"</span>
  <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Github users, async via api.github.com"</span>
  <span class="hljs-attr">@change</span>=<span class="hljs-string">"getGitresults"</span>
  <span class="hljs-attr">:items</span>=<span class="hljs-string">"gitItems"</span>
  <span class="hljs-attr">:item-render</span>=<span class="hljs-string">"itemRendergit"</span>
  <span class="hljs-attr">:add-format</span>=<span class="hljs-string">"githubCallback"</span> &gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">n3-typeahead</span>&gt;</span>
</code></pre>
</div>
<h3>Params</h3>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td><code v-pre="">Array</code></td>
<td>-</td>
<td>locale data</td>
</tr>
<tr>
<td>limit</td>
<td><code v-pre="">Number</code></td>
<td><code v-pre="">8</code></td>
<td>the limited number of items</td>
</tr>
<tr>
<td>items</td>
<td><code v-pre="">Array</code></td>
<td><code v-pre="">[]</code></td>
<td>suggestion items，for asyc</td>
</tr>
<tr>
<td>async</td>
<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">false</code></td>
<td>whether is async or not</td>
</tr>
<tr>
<td>item-render</td>
<td><code v-pre="">Function</code></td>
<td>-</td>
<td>itemRender function</td>
</tr>
<tr>
<td>add-format</td>
<td><code v-pre="">Function</code></td>
<td>-</td>
<td>format function to get right data</td>
</tr>
<tr>
<td>dropdown-width</td>
<td><code v-pre="">String</code></td>
<td><code v-pre="">220px</code></td>
<td>width of dropdown</td>
</tr>
<tr>
<td>dropdown-height</td>
<td><code v-pre="">String</code></td>
<td><code v-pre="">300px</code></td>
<td>max height of dropdown</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table>
<thead>
<tr>
<th>name</th>
<th>description</th>
<th>params</th>
</tr>
</thead>
<tbody>
<tr>
<td>blur</td>
<td>blur</td>
<td>-</td>
</tr>
<tr>
<td>focus</td>
<td>focus</td>
<td>-</td>
</tr>
<tr>
<td>change</td>
<td>change</td>
<td>(currentValue)</td>
</tr>
</tbody>
</table>
<p>PS: params of form components，look <a href="#n3FormDocs">Form</a> </p>
</div>
</template>

</section></template>

<script>
export default {
  data () {
    return {
      items: [],
      gitItems: [],
      value: 'Alabama',
      USstate: ['Alabama', 'Alaska', 'Arizona', 'Arkansas', 'California', 'Colorado', 'Connecticut', 'Delaware', 'Florida', 'Georgia', 'Hawaii', 'Idaho', 'Illinois', 'Indiana', 'Iowa', 'Kansas', 'Kentucky', 'Louisiana', 'Maine', 'Maryland', 'Massachusetts', 'Michigan', 'Minnesota', 'Mississippi', 'Missouri', 'Montana', 'Nebraska', 'Nevada', 'New Hampshire', 'New Jersey', 'New Mexico', 'New York', 'North Dakota', 'North Carolina', 'Ohio', 'Oklahoma', 'Oregon', 'Pennsylvania', 'Rhode Island', 'South Carolina', 'South Dakota', 'Tennessee', 'Texas', 'Utah', 'Vermont', 'Virginia', 'Washington', 'West Virginia', 'Wisconsin', 'Wyoming']
    }
  },
  methods: {
    itemRender (item) {
      return item.formatted_address
    },
    getResult (query) {
      let self = this
      $.ajax({
        url: 'https://maps.googleapis.com/maps/api/geocode/json?address=' + query,
        success (ret) {
          self.items = ret.results
        }
      })
    },
    getGitresults (query) {
      let self = this
      $.ajax({
        url: 'https://api.github.com/search/users?q=' + query,
        success (ret) {
          self.gitItems = ret.items
        }
      })
    },
    itemRendergit (item) {
      return `<div><img width="18px" height="18px" src="${item.avatar_url}"/> <span>${item.login}</span></div>`
    },
    googleCallback (item) {
      return item.formatted_address
    },
    githubCallback (item) {
      return item.login
    }
  }
}
</script>


