<td>Empty Content.</td>
<td>Show text when the data is empty</td>
</tr>
<tr>
<td>node-key</td>
<td><code v-pre="">String</code></td>
<td>—</td>
<td>Each tree node is used as a unique attribute, and the whole tree should be unique</td>
</tr>
<tr>
<td>props</td>
<td></td>
<td><code v-pre="">Object</code></td>
<td>—</td>
</tr>
<tr>
