<td>angle-down</td>
<td>open icon</td>
</tr>
<tr>
<td>load</td>
<td><code v-pre="">Function</code></td>
<td>function(node, resolve) {}</td>
<td>Methods for loading subtree data</td>
</tr>
<tr>
<td>render-content</td>
<td><code v-pre="">Function</code></td>
<td>function(h, { node }) {}</td>
<td>render function</td>
</tr>
<tr>
<td>highlight-current</td>
<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">false</code></td>
<td>Whether to highlight the currently selected node</td>
</tr>
<tr>
<td>current-node-key</td>
<td><code v-pre="">String, Number</code></td>
<td>—</td>
<td>Currently select the node's key, write only attribute</td>
</tr>
<tr>
<td>default-expand-all</td>
<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">false</code></td>
<td>Whether to expand all nodes by default</td>
</tr>
