<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td><code v-pre="">String</code></td>
<td>label</td>
<td>Specifies that the node tag is a property value for the node object</td>
</tr>
<tr>
<td>children</td>
<td><code v-pre="">String</code></td>
<td>children</td>
<td>Specifies that the subtree is a property value for the node object</td>
</tr>
</tbody>
</table>
<h3>Methods</h3>
<table>
<thead>
<tr>
<th>name</th>
<th>description</th>
<th>params</th>
</tr>
</thead>
<tbody>
<tr>
<td>filter</td>
<td>Filter the tree nodes</td>
<td>Receive an arbitrary type of parameter, this parameter will be in the filter-node-method as the first parameter</td>
</tr>
<tr>
<td>getCheckedNodes</td>
<td>If the node can be selected (ie <code v-pre="">show-checkbox</code> is<code v-pre="">true</code>), the array of the currently selected nodes is returned</td>
<td>LeafOnly) to receive a boolean type of parameters, if the <code v-pre="">true</code> only return to the selected leaf node, the default value is<code v-pre="">false</code></td>
</tr>
<tr>
<td>setCheckedNodes</td>
<td>Set the currently checked node, use this method must set the node-key attribute</td>
<td>(Nodes) Receive an array of checked node data</td>
</tr>
<tr>
<td>getCheckedKeys</td>
<td>If the node can be selected (ie <code v-pre="">show-checkbox</code> is<code v-pre="">true</code>), the array of the currently selected nodes is returned</td>
<td>(LeafOnly) to receive a boolean type of parameters, if the <code v-pre="">true</code> only return the selected leaves node keys, the default value is<code v-pre="">true</code></td>
</tr>
<tr>
<td>setCheckedKeys</td>
<td>Use the keys to set the currently checked node, and use this method to set the node-key attribute</td>
<td>(Keys, leafOnly) to receive two parameters, 1. check the node's key array 2. boolean type of parameters, if the <code v-pre="">true</code> only set the leaves node selected state, the default value is<code v-pre="">true</code></td>
</tr>
<tr>
<td>setChecked</td>
<td>Through the key / data set a node check the state, use this method must set the node-key attribute</td>
<td>(key/data, checked, deep)Receive three parameters, 1. check the node's key or data 2. boolean type, the node is selected 3. boolean type, whether to set the child node, the default is false</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table>
<thead>
<tr>
<th>name</th>
<th>description</th>
<th>params</th>
</tr>
</thead>
<tbody>
<tr>
<td>node-click</td>
<td>The callback when the node is clicked</td>
<td>A total of three parameters, as follows: <code v-pre="">data</code> array to the object attribute corresponding to the node, the corresponding node Node, node assembly itself.</td>
</tr>
<tr>
<td>check-change</td>
<td>The node selects the callback when the state changes</td>
<td>A total of three parameters, as follows: <code v-pre="">data</code> array to the object attribute corresponding to the node, whether the node itself is checked, whether there is the selected node in the subtree node</td>
</tr>
<tr>
<td>current-change</td>
<td>The event that was triggered when the node was currently selected</td>
<td>A total of two parameters, followed by: the current node data, the current node Node object</td>
</tr>
</tbody>
</table>
</div>
</template>
</section></template>

<script>
  export default {
    data() {
      return {
        data: [{
          label: 'folder1',
          children: [{
            label: 'file1'
          }]
        }, {
          label: 'folder2',
          children: [{
            label: 'file2'
          }, {
            label: 'file3'
          }]
        }, {
          label: 'folder3',
          children: [{
            label: 'folder4',
            children: [{
              label: 'file6'
            }]
          }, {
            label: 'file4'
          }, {
            label: 'file5'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        regions: [{
          'name': '/root'
        }, {
          'name': '/home'
        }],
        props: {
          label: 'name',
          children: 'zones'
        },
        count: 1
      }
    },
    methods: {
      handleCheckChange(data, checked, indeterminate) {
        console.log(data, checked, indeterminate)
      },
      handleNodeClick(data) {
        console.log(data)
      },
       loadNode(node, resolve) {
        if (node.level === 0) {
          return resolve([{ name: '/root' }, { name: '/home' }])
        }
        if (node.level > 3) return resolve([])

        var hasChild
        if (node.data.name === '/root') {
          hasChild = true
        } else if (node.data.name === '/home') {
          hasChild = false
        } else {
          hasChild = Math.random() > 0.5
        }

        setTimeout(() => {
          var data
          if (hasChild) {
            data = [{
              name: 'folder' + this.count++
            }, {
              name: 'folder' + this.count++
            }]
          } else {
            data = []
          }

          resolve(data)
        }, 500)
      }
    }
  }
</script>


