<h3>Params</h3>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">false</code></td>
<td>whether a Alert is visible or not</td>
</tr>
<tr>
<td>icon</td>
<td><code v-pre="">String</code></td>
<td>-</td>
<td>icon</td>
</tr>
<tr>
<td>dismissable</td>
<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">false</code></td>
<td>whether a close button is visible or not</td>
</tr>
<tr>
<td>type</td>
<td><code v-pre="">String</code>, one of <code v-pre="">default</code> <code v-pre="">success</code> <code v-pre="">info</code> <code v-pre="">warning</code> <code v-pre="">danger</code></td>
<td><code v-pre="">success</code></td>
<td>type of Alert</td>
</tr>
<tr>
<td>duration</td>
<td><code v-pre="">Number</code></td>
<td><code v-pre="">0</code></td>
<td>duration setting ,you can set it as <code v-pre="">0</code> to prevent Alert to be closed</td>
</tr>
<tr>
<td>width</td>
<td><code v-pre="">String</code></td>
<td></td>
<td></td>
</tr>
<tr>
<td>small</td>
<td><code v-pre="">Boolean</code></td>
<td></td>
<td>small style</td>
</tr>
<tr>
<td>placement</td>
<td><code v-pre="">String</code>. 如: <code v-pre="">top</code>, <code v-pre="">top-right</code>,<code v-pre="">top-left</code>, <code v-pre="">bottom</code>,<code v-pre="">bottom-left</code>, <code v-pre="">bottom-right</code></td>
<td></td>
<td>placement of Alert</td>
</tr>
<tr>
<td>description</td>
<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">false</code></td>
<td>whether a description is visible or not</td>
</tr>
<tr>
<td>message</td>
<td><code v-pre="">String</code></td>
<td></td>
<td>work when description is <code v-pre="">true</code></td>
</tr>
</tbody>
</table>
