</style>
<script>
  import n3Alert from 'src/Alert/n3Alert.vue'
  import n3Button from 'src/Button/n3Button.vue'

  export default {
    methods: {
      openRight () {
        this.$refs.alertRight.open()
      },
      openTop () {
        this.$refs.alertTop.open()
      },
      close () {
        console.log('you click close button')
      },
      open () {
        this.n3Alert({
          content: 'success',
          type: 'success',
          placement: 'top-right',
          duration: 2000,
          width:'200px'
        })
      }
    },
    components: {
      n3Alert,
      n3Button
    }
  }
</script>


