<pre v-pre=""><code v-pre="" class="language-javascript">
{
  <span class="hljs-attr">name</span>:{
    <span class="hljs-string">'Jack'</span>: {<span class="hljs-attr">rowspan</span>: <span class="hljs-number">2</span>},
    <span class="hljs-string">'Tom'</span>: {<span class="hljs-attr">rowspan</span>: <span class="hljs-number">0</span>}
  }
}

</code></pre>
<h4>pagination</h4>
<pre v-pre=""><code v-pre="" class="language-javascript">
{
  <span class="hljs-attr">current</span>:<span class="hljs-number">5</span>,
  <span class="hljs-attr">total</span>:<span class="hljs-number">10</span>,
  <span class="hljs-attr">pagesize</span>:<span class="hljs-number">10</span>
}

</code></pre>
<h4>@change</h4>
<pre v-pre=""><code v-pre="" class="language-javascript">
<span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">pagination,query,sort,filter</span>)</span>{
  <span class="hljs-keyword">var</span> data = {
    <span class="hljs-attr">start</span>:(pagination.current - <span class="hljs-number">1</span>) * pagination.pagesize,
    <span class="hljs-attr">limit</span>:pagination.pagesize
  },
  self = <span class="hljs-keyword">this</span>;

  <span class="hljs-keyword">this</span>.loading = <span class="hljs-literal">true</span>
  $.ajax({
    <span class="hljs-attr">url</span>:<span class="hljs-string">''</span>,
    <span class="hljs-attr">data</span>:data,
    <span class="hljs-attr">success</span>:<span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">result</span>)</span>{
        self.loading = <span class="hljs-literal">false</span>
        self.source = result.list
        self.pagination.total = result.total
    }
  })
}

</code></pre>
<h3>Params</h3>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>fixed-columns</td>
<td><code v-pre="">Boolean</code></td>
<td>-</td>
<td>fix table header</td>
</tr>
<tr>
<td>height</td>
<td><code v-pre="">String</code></td>
<td>-</td>
<td>height，work width <code v-pre="">fixed-columns</code></td>
</tr>
<tr>
<td>width</td>
<td><code v-pre="">String</code></td>
<td>-</td>
<td>width， conflict with  <code v-pre="">fixed-columns</code></td>
</tr>
<tr>
<td>async</td>
<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">false</code></td>
<td>Asynchronous loading, with the change event to request server data</td>
</tr>
<tr>
<td>merge-rule</td>
<td><code v-pre="">Object</code></td>
<td>-</td>
<td>Merge rules</td>
</tr>
<tr>
<td>columns</td>
<td><code v-pre="">Array</code></td>
<td>-</td>
<td>Header array, set field name, description, support sort, display, render, etc.</td>
</tr>
<tr>
<td>selection</td>
<td><code v-pre="">Object</code></td>
<td>-</td>
<td><code style="color:red">Use <code v-pre="">getCheckboxProps</code> to initialize the state</code></td>
</tr>
<tr>
