<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">true</code></td>
<td>search</td>
</tr>
<tr>
<td>filter</td>
<td><code v-pre="">Boolean</code></td>
<td><code v-pre="">true</code></td>
<td>Whether to support field filtering, columns for the object to add fiiter to specify its filter</td>
</tr>
<tr>
<td>filter-list</td>
<td><code v-pre="">Array</code></td>
<td>-</td>
