<table>
<thead>
<tr>
<th>name</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>When the page, search, filter sorting and other conditions change when the callback function for the server operation</td>
</tr>
</tbody>
</table>
</div>
</template>
</section></template>

<script>
export default {
  methods: {
    change (p, q, s, f) {
      console.log(p, q, s, f)
    },
    refresh () {
      this.source = [{
        key: 'Jack',
        name: 'Jack',
        age: 25,
        department: 'IT'
      }, {
        key: 'Tom',
        name: 'Tom',
        age: 33,
        department: 'IT'
      }, {
        key: 'Eric',
        name: 'Eric',
        age: 12,
        department: 'Sales'
      }, {
        key: 'Kate',
        name: 'Kate',
        age: 25,
        department: 'Finance'
      }, {
        key: 'Tim',
        name: 'Tim',
        age: 33,
        department: 'Sales'
      }, {
        key: 'Li',
        name: 'Li',
        age: 22,
        department: 'Finance'
      }, {
        key: 'King',
        name: 'King',
        age: 20,
        department: 'Product'
      }, {
        key: 'Lucy',
        name: 'Lucy',
        age: 32,
        department: 'Product'
      }]
    },
    del (key) {
      for (var i in this.source) {
        if (key === this.source[i]['key']) {
          this.source.splice(i, 1)
        }
      }
    }
  },

  data () {
    return {
      selection: {
        checkRows: [],
        onSelect (record, checked, checkRows) {},
        onSelectAll (checked, checkRows, changeRows) {},
        getCheckboxProps (record) {
          if (record.key == 2) {
            return {
              checked: true,
              disabled: true
            }
          } else {
            return {
              disabled: false
            }
          }
        }
      },
      loading: false,
      pagination: {
        current: 1,
        total: 10,
        pagesize: 10
      },
      filterList: [{
        title: 'name',
        dataIndex: 'name',
        options: [{value: 'Tom', label: 'Tom'}, {value: 'Jack', label: 'Jack'}],
      }],
      columns: [{
        title: 'name',
        dataIndex: 'name',
        sort: true,
        width: '100px',
        filter: true
      }, {
        title: 'age',
        dataIndex: 'age',
        sort: true,
        sortType: 'DESC',
        sortMethod (x, y) { return x.age - y.age },
        filter: true,
        width: '150px',
        render: (text, record) => {
          return `<a href="javascript:;">${text}</a>`
        }
      }, {
        title: 'department',
        dataIndex: 'department',
        width: '250px'
      }, {
        title: 'operating',
        dataIndex: '',
        render: (text, record, index) => {
          return `<span class="item">
                    <a href="javascript:;" @click="del('${record.key}','${index}')" style="color:#41cac0">delete</a>
                  </span>`
        }
      }],
      source: [{
        key: 'Jack',
        name: 'Jack',
        age: 25,
        department: 'IT'
      }, {
        key: 'Tom',
        name: 'Tom',
        age: 33,
        department: 'IT'
      }, {
        key: 'Eric',
        name: 'Eric',
        age: 12,
        department: 'Sales'
      }, {
        key: 'Kate',
        name: 'Kate',
        age: 25,
        department: 'Finance'
      }, {
        key: 'Tim',
        name: 'Tim',
        age: 33,
        department: 'Sales'
      }, {
        key: 'Li',
        name: 'Li',
        age: 22,
        department: 'Finance'
      }, {
        key: 'King',
        name: 'King',
        age: 20,
        department: 'Product'
      }, {
        key: 'Lucy',
        name: 'Lucy',
        age: 32,
        department: 'Product'
      }]
    }
  }
}
</script>


