<n3-multiple-input @focus="focus" @inputChange="getResult" :async="true" v-model="a.list" :format="format" :item-render="itemRender" :items="items">
</n3-multiple-input>
</div>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">n3-multiple-input</span>
