<span class="hljs-tag">&lt;/<span class="hljs-name">n3-multiple-input</span>&gt;</span>
</code></pre>
<pre v-pre=""><code v-pre="" class="language-javascript">methods: {
  focus () {
    <span class="hljs-built_in">console</span>.log(<span class="hljs-number">1</span>)
  },
  itemRender (item) {
    <span class="hljs-keyword">return</span> item.formatted_address
  },
  getResult (query) {
    <span class="hljs-built_in">console</span>.log(query, <span class="hljs-number">1</span>)
    <span class="hljs-keyword">let</span> self = <span class="hljs-keyword">this</span>
    $.ajax({
      <span class="hljs-attr">url</span>: <span class="hljs-string">'https://maps.googleapis.com/maps/api/geocode/json?address='</span> + query,
      success (ret) {
        self.items = ret.results
      }
    })
  },
  format (item, index) {
    <span class="hljs-keyword">let</span> content = item.formatted_address
    <span class="hljs-keyword">return</span> content
  }
}
</code></pre>
<h3>Params</h3>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td><code v-pre="">Array</code></td>
<td>-</td>
<td>locale data</td>
</tr>
<tr>
<td>format</td>
<td><code v-pre="">Function</code></td>
<td>-</td>
<td>format function</td>
</tr>
<tr>
<td>limit</td>
<td><code v-pre="">Number</code></td>
<td><code v-pre="">8</code></td>
<td>the limited number of items</td>
</tr>
<tr>
<td>items</td>
<td><code v-pre="">Array</code></td>
<td><code v-pre="">[]</code></td>
<td>suggestion items，for asyc</td>
</tr>
